.loginbg {
  width: 100vw;
  height: 100vh;
  background: url("https://cdn.pixabay.com/photo/2016/05/05/02/37/sunset-1373171__480.jpg");
  background-size: cover;
  margin-top: -27px;
  margin-left: -1vw;
  background-color: rgba(0, 0, 0, 0.233);
  background-blend-mode: multiply;
  background-attachment: fixed;
  position: absolute;
}
.login-panel {
  background-color: var(--prim-color);
  width: 33vw;
  border: 1px solid grey;
  border-radius: 5px;
  margin: 25vh auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}
.login-panel h1 {
  width: 100%;
}
.login-panel input {
  width: auto;
  height: 30px;
  border-radius: 2px;
}
